<template>
  <a-modal
    title="选择分类移动"
    :confirmLoading="confirmLoading"
    :visible="visible"
    @ok="handleOk"
    @cancel="handleCancel"
  >
    <Menu ref="menu" class="menus" :list="list" :tip="false"></Menu>
  </a-modal>
</template>
<script>
import Menu from "@/views/products/menu";
import { batchMove } from "@/api/product";
export default {
  components: { Menu },
  props: ["list", "selectList", "products"],
  data() {
    return {
      visible: false,
      confirmLoading: false
    };
  },
  methods: {
    init() {
      this.visible = true;
      setTimeout(() => {
        this.$refs.menu.init();
      }, 0);
    },
    async handleOk() {
      if (this.selectList.length==0)
        return this.$message.warning("请选择产品");
      this.confirmLoading = true;
      let productCodes = this.products
          .filter((v, i) => this.selectList.some(m => m == i))
          .map(v => v.productCode),
        clasCode = this.$refs.menu.selectItem.value,
        res = await batchMove({
          clasCode,
          productCodes
        });
      if (res.data.code != this.$global.successCode) {
        this.$message.warning(res.data.message);
        this.confirmLoading = false;
        return;
      }
      this.$message.success(res.data.message);
      this.confirmLoading = false;
      this.visible = false;
      this.$emit('clear')
      this.$emit("getProduct");
    },
    handleCancel() {
      this.visible = false;
    }
  }
};
</script>
<style lang="less" scoped>
@scroll-width: 0.5rem;
@scroll-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
@slide-color: #ddd;
@track-color: #eee;
.menus {
  height: 18.75rem;
  overflow-y: auto;
}
.menus::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: @scroll-width;
  /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px;
}

.menus::-webkit-scrollbar-thumb {
  /*滚动条中的滚动块*/
  border-radius: @scroll-width;
  -webkit-box-shadow: @scroll-shadow;
  background: @slide-color;
}

.menus::-webkit-scrollbar-track {
  /*滚动条里面的轨道*/
  -webkit-box-shadow: @scroll-shadow;
  border-radius: @scroll-width;
  background: @track-color;
}
</style>
