<template>
  <div class="submenu">
    <div
      class="item dotted"
      v-for="(v,i) in Menus"
      :key="i"
      @click.stop="select(getKeys(i,true),v)"
    >
      <div
        class="title"
        @mouseenter.stop="throttle(mouseenter(getKeys(i,true)))"
        @mouseleave.stop="mouseleave(false)"
        :style="v.open&&i!=Menus.length-1?'padding-bottom:0px':''"
      >
        <i class="icon">{{v.children?v.open?'-':'+':''}}</i>
        <span
          class="text"
          :class="{'hover':HoverIndex==getKeys(i,true),'select':selectKey==getKeys(i,true)}"
        >{{v.title}}</span>
        <div class="tip" v-if="v.children&&useTip" v-show="HoverIndex==getKeys(i,true)">
          <div class="tip-item" v-for="(m,n) in v.children" :key="n">
            <span
              @click.stop="select(getKeys(i,true,[n]),m)"
              class="tip-title"
              :class="{'hover-tip':tipIndex==getKeys(i,true,[n]),'select-tip':selectKey==getKeys(i,true,[n])}"
              @mouseenter.stop="throttle(mouseenter(getKeys(i,true,[n]),true))"
              @mouseleave.stop="mouseleave(true)"
            >{{m.title}}</span>
            <ul class="tip-texts" v-if="m.children">
              <li
                @click.stop="select(getKeys(i,true,[n,index]),val)"
                :class="{'hover-tip':tipIndex==getKeys(i,true,[n,index]),'select-tip':selectKey==getKeys(i,true,[n,index])}"
                @mouseenter.stop="throttle(mouseenter(getKeys(i,true,[n,index]),true))"
                @mouseleave.stop="mouseleave(true)"
                class="tip-text"
                v-for="(val,index) in m.children"
                :key="index"
              >{{val.title}}</li>
            </ul>
          </div>
        </div>
      </div>
      <div class="children" v-if="v.children&&!useTip" v-show="v.open">
        <submenu
          :menus="v.children"
          :floor="floor+1"
          :selectKey="selectKey"
          :indexs="getKeys(i)"
          @select="select"
          :tip="tip"
        />
      </div>
    </div>
  </div>
</template>
<script>
import submenu from "./submenu";
export default {
  name: "submenu",
  props: {
    menus: {
      type: Array,
      default: []
    },
    floor: {
      type: Number,
      default: 1
    },
    indexs: {
      type: Array,
      default: () => []
    },
    selectKey: {
      type: String,
      default: "0"
    },
    tip: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      _lastTime: 0,
      HoverIndex: null,
      tipIndex: null,
      useTip: false,
      isFormat: false,
      Menus: []
    };
  },
  watch: {
    menus() {
      // console.log('watch menus')
      // console.log(this.menus)
      this.formatMenus();
    }
  },
  mounted() {
    // console.warn("sub mounted");
    this.formatMenus();
    if (this.tip) this.useTip = this.floor > 2;
  },
  methods: {
    getKeys(i, str = false, tips = []) {
      let arr = JSON.parse(JSON.stringify(this.indexs));
      arr.push(i);
      if (tips[0] != undefined) arr = arr.concat(tips);
      return str ? arr.join("-") : arr;
    },
    select(key, v) {
      if (!this.useTip && v.children) {
        v.open = !v.open;
      }
      this.$emit("select", key, JSON.parse(JSON.stringify(v)));
    },
    formatMenus() {
      if (this.isFormat) return;
      this.isFormat = true;
      let data = JSON.parse(JSON.stringify(this.menus));
      data.forEach(v => {
        if (v.children) v.open = false;
      });
      this.Menus = data;
      this.isFormat = false;
    },
    mouseenter(i, tip = false) {
      if (tip) return (this.tipIndex = i);
      this.HoverIndex = i;
    },
    mouseleave(tip = false) {
      if (tip) return (this.tipIndex = null);
      this.HoverIndex = null;
    },
    throttle(handler, wait = 1000 / 40) {
      return () => {
        var nowTime = new Date().getTime();
        if (nowTime - this._lastTime > wait) {
          handler.apply(this, arguments);
          this._lastTime = nowTime;
        }
      };
    }
  }
};
</script>
<style lang="less" scoped>
@icon-width: 16px;
@primary-color: rgb(0, 170, 238);
.submenu {
  padding-left: @icon-width;
  .dotted:not(:last-child) {
    > div:not(:first-child) {
      border-left: 1px dotted #ddd;
      // border-top: 1px solid #ff0036;
      padding-top: 10px;
    }
  }
  .item {
    .title {
      cursor: pointer;
      position: relative;
      display: flex;
      padding: 0;
      align-items: center;
      padding-bottom: 10px;
      .text {
        font-size: 14px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        padding: 0 0.25rem;
        border-radius: 0.25rem;
      }
      .icon {
        background-color: #fff;
        position: relative;
        left: -@icon-width / 2;
        width: @icon-width;
        height: @icon-width;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid #ddd;
      }
      .tip {
        font-size: 14px;
        position: absolute;
        z-index: 1000;
        top: 0%;
        left: 100%;
        width: 800px;
        background-color: #0b1822;
        color: #fff;
        padding: 1rem;
        // box-shadow: 0 0 2px @primary-color;
        .tip-item {
          display: flex;
          align-items: flex-start;
          .tip-title {
            margin: 0.625rem 1rem;
            flex-shrink: 0;
            padding: 0.3125rem 0.625rem;
            border-radius: 0.25rem;
          }
          .tip-texts {
            flex-grow: 1;
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            flex-wrap: wrap;
            .tip-text {
              background-color: #19262f;
              width: 18%;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
              padding: 0.3125rem 0.625rem;
              margin: 0.625rem 0;
              border-radius: 0.25rem;
            }
          }
        }
      }
    }
    .hover,
    .select {
      color: @primary-color;
    }
    .hover-tip,
    .select-tip {
      background-color: @primary-color !important;
    }
  }
}
</style>
