<template>
  <a-modal
    class="code-modal"
    title="内部编码对应"
    :confirmLoading="confirmLoading"
    :visible="visible"
    @ok="handleOk"
    @cancel="handleCancel"
  >
    <a-table class="table" :pagination="false" :dataSource="data" :rowKey="(v,i)=>i">
      <a-table-column title="企业名称" data-index="businessName">
        <template slot-scope="t,v,i">
          <!-- <a-input class="input" size="small" aria-placeholder="企业名称" v-model="v.businessName"></a-input> -->
          <a-select :value="v.businessName" @change="changeSelect" class="input">
            <a-select-option v-for="(val,index) in companys" :value="index+'-'+i" :key="index">{{val.businessName}}</a-select-option>
          </a-select>
        </template>
      </a-table-column>
      <a-table-column title="对应内部编码" data-index="businessProductCode">
        <template slot-scope="t,v,i">
          <a-input
            class="input"
            size="small"
            aria-placeholder="对应内部编码"
            v-model="v.businessProductCode"
          ></a-input>
        </template>
      </a-table-column>
      <a-table-column title="操作">
        <template slot-scope="t,v,i">
          <a class="del" href="javascript:;" @click="onDelete(v,i)">删除</a>
        </template>
      </a-table-column>
    </a-table>
    <div class="add" @click="add">
      添加
      <a-icon type="plus" />
    </div>
  </a-modal>
</template>
<script>
import { getMatch, putMatch, getCompany } from "@/api/product";
export default {
  data() {
    return {
      visible: false,
      confirmLoading: false,
      data: [],
      companys:[],
      id: null,
      delList: []
    };
  },
  methods: {
    init(id) {
      this.data = [];
      this.visible = true;
      this.id = id;
      this.delList = [];
      this.getData();
      this.getCompany();
    },
    changeSelect(value){
      let [ci,di]=value.split('-')
      let data=this.companys.find((v,ind)=>ind==ci)
      Object.assign(this.data[di],data)
    },
    add() {
      let { data } = this,
        len = data.length - 1;
      if (
        data.length > 0 &&
        (!data[len].businessName || !data[len].businessProductCode)
      )
        return this.$message.warning("信息未填写");
      this.data.push({
        businessName: "",
        businessProductCode: "",
        productCode:this.id,
        isAdd: true
      });
    },
    async getCompany(key='') {
      let res = await getCompany(key);
      if (res.data.code != this.$global.successCode)
        return this.$message.warning(res.data.message);
      this.companys = res.data.data;
    },
    async getData(id = this.id) {
      let res = await getMatch(id);
      if (res.data.code != this.$global.successCode)
        return this.$message.warning(res.data.message);
      this.data = res.data.data||[];
    },
    async onDelete(v, i) {
      if (!v.isAdd) this.delList.push(v);
      this.data = this.data.filter((v, ind) => ind != i);
    },
    async handleOk() {
      this.confirmLoading = true;
      let res = await putMatch({
        delProductCompanyRelationForm:this.delList,
        productCompanyRelationForm:this.data
      });
      if (res.data.code != this.$global.successCode) {
        this.$message.warning(res.data.message);
        this.confirmLoading = false;
        return;
      }
      this.$message.success(res.data.message);
      this.confirmLoading = false;
      this.visible = false;
    },
    handleCancel() {
      this.visible = false;
    }
  }
};
</script>
<style lang="less">
@scroll-width: 0.5rem;
@scroll-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
@slide-color: #ddd;
@track-color: #eee;
.code-modal {
  ::-webkit-scrollbar {
    /*滚动条整体样式*/
    width: @scroll-width;
    /*高宽分别对应横竖滚动条的尺寸*/
    height: 1px;
  }

  ::-webkit-scrollbar-thumb {
    /*滚动条中的滚动块*/
    border-radius: @scroll-width;
    -webkit-box-shadow: @scroll-shadow;
    background: @slide-color;
  }

  ::-webkit-scrollbar-track {
    /*滚动条里面的轨道*/
    -webkit-box-shadow: @scroll-shadow;
    border-radius: @scroll-width;
    background: @track-color;
  }
  .ant-table-header {
    overflow-y: auto !important;
    margin-bottom: 0px !important;
    padding-bottom: 0px !important;
  }
  .ant-modal-body {
    padding: 0px;
    overflow-y: auto;
    max-height: 300px;
  }
  .ant-table-body {
    th:first-child,
    td:first-child {
      padding: 16px 30px;
    }
    th {
      color: #666;
      font-weight: bold;
    }
    .del {
      color: #00aaee;
    }
  }
}
</style>
<style lang="less" scoped>
@primary-color: #00aaee;
.code-modal {
  .add {
    cursor: pointer;
    color: @primary-color;
    text-align: center;
    padding: 16px 0;
  }
  .add:hover {
    opacity: 0.8;
  }
  .input {
    width: 150px;
  }
}
</style>

