<template>
  <div class="main">
    <a-breadcrumb>
      <a-breadcrumb-item>首页</a-breadcrumb-item>
      <a-breadcrumb-item>零部件管理</a-breadcrumb-item>
    </a-breadcrumb>
    <h2 class="title">零部件管理</h2>
    <div class="content_box" style="padding:0px 20px">
      <a-layout>
        <a-layout-sider
          class="content_nav"
          style="background:none;flex: 0 0 260px;min-width:260px;max-width: 260px;width: 260px; "
        >
          <Menu ref="menu" :list="treeData" @select="onSelectree" :bread="breadcrumb"></Menu>
          <!-- <a-tree
            v-if="treeData.length"
            :defaultExpandAll="true"
            @select="onSelectree"
            :treeData="treeData"
          />-->
        </a-layout-sider>
        <a-layout-content>
          <div class="content_table">
            <breadcrumb ref="breadcrumb" />
            <screen ref="filter" @select="setScreenList" />
            <div class="clear"></div>
            <div class="search_box3">
              <a-button type="primary" @click="opp1">添加零部件</a-button>
              <a-button type="primary" @click="opp2">高级匹配查询</a-button>
              <!-- <a-button icon="download" @click="visible5=true">批量导入</a-button> -->
              <a-button @click="$refs.importExcel.init(false)">批量导入</a-button>
              <a-button @click="$refs.moveclass.init()">批量移动</a-button>
              <a-popconfirm
                title="确定删除数据吗?"
                @confirm="() => batchDelete()"
                cancelText="取消"
                okText="确定"
              >
                <a-button>批量删除</a-button>
              </a-popconfirm>
              <a-button @click="$refs.importExcel.init(true)">批量导入编码对应</a-button>
              <a-input-search
                class="searchinput"
                placeholder="搜索..."
                style="width: 200px"
                v-model="searchKey"
                @change="getProductLists(selectedKeys)"
              />
            </div>
            <a-layout style="max-height:calc(100vh - 310px);padding: 0">
              <a-table
                :columns="columns"
                :dataSource="productdata"
                :pagination="showPagination"
                :rowSelection="{selectedRowKeys: selectedRowKeys, onChange: onSelectChange}"
                :rowKey="(v,i)=>{return i}"
                style="height:100%;overflow:auto"
              >
                <template slot="productName" slot-scope="text, record">
                  <span class="lineEllipsis2">{{text}}</span>
                </template>
                <template slot="productBrand" slot-scope="text, record">
                  <span class="lineEllipsis2">{{text}}</span>
                </template>
                <template slot="productSpecs" slot-scope="text, record">
                  <span class="lineEllipsis2">{{text}}</span>
                </template>
                <template slot="productMaterialQuality" slot-scope="text, record">
                  <span class="lineEllipsis2">{{text}}</span>
                </template>
                <template slot="productImprint" slot-scope="text, record">
                  <span class="lineEllipsis2">{{text}}</span>
                </template>
                <template slot="productLength" slot-scope="text, record">
                  <span class="lineEllipsis2">{{text}}</span>
                </template>
                <template slot="productDescribe" slot-scope="text, record">
                  <span class="lineEllipsis2">{{text}}</span>
                </template>
                <template slot="packStandards" slot-scope="text, record">
                  <span class="lineEllipsis2">{{text}}</span>
                </template>
                <template slot="action" slot-scope="text,record">
                  <a-popconfirm
                    title="确定删除数据吗?"
                    @confirm="() =>  deleteProductOnly(record)"
                    cancelText="取消"
                    okText="确定"
                  >
                    <a href="javascript:;">
                      <a-icon type="delete" />删除
                    </a>
                  </a-popconfirm>
                  <a href="javascript:;" @click="$refs.matchCode.init(record.productCode)">内部编码对应</a>
                </template>
                <template slot="productCode" slot-scope="text,record">
                  <span class="lineEllipsis2">
                    <a href="javascript:;" @click="opp(record)">{{text}}</a>
                  </span>
                </template>
                <template slot="productChart" slot-scope="text,record">
                  <span class="lineEllipsis2">
                    <a href="javascript:;" @click="$refs.preImgs.init(record)">{{text}}</a>
                  </span>
                </template>
              </a-table>
            </a-layout>
            <a-layout>
              <div class="pagination-box">
                <a-pagination
                  style="margin-left:10px"
                  showSizeChanger
                  :showTotal="total => `共 ${pageTotal} 条`"
                  showQuickJumper
                  :pageSize.sync="pageSize"
                  @showSizeChange="onShowSizeChange"
                  :total="pageTotal"
                  @change="onChange"
                  v-model="current"
                />
              </div>
            </a-layout>
          </div>
        </a-layout-content>
      </a-layout>
    </div>

    <!-- 图纸展示弹窗 -->
    <a-modal
      :title="Title1"
      :visible="visible"
      :confirmLoading="confirmLoading"
      @cancel="visible=false"
      :footer="null"
    >
      <img style="width: 100%" :src="productImage" />
    </a-modal>

    <!-- 批量导入弹窗 -->
    <a-modal
      title=" 批量导入"
      :visible="visible5"
      :footer="null"
      class="dialog_content"
      width="600px"
      :confirmLoading="confirmLoading"
      @cancel="visible5=false"
    >
      <div class="import-introduce" element-loading-text="数据导入中...">
        <div class="import-introduce-title">根据导入模版填写信息进行导入，即可完成批量添加</div>

        <div class="import-introduce-step">
          <div class="import-introduce-step-left">第一步：</div>
          <div class="import-introduce-step-rigth">
            <div class="import-introduce-step-rigth-top">
              <a :href="fileImportUrl2" style="cursor: pointer">下载Execl导入模版</a>
            </div>
            <div class="import-introduce-step-right-bottom">若已下载过模版，可直接进行导入数据</div>
          </div>
        </div>

        <div class="import-introduce-step">
          <div class="import-introduce-step-left">第二步：</div>
          <div class="import-introduce-step-rigth">
            <div class="import-introduce-step-rigth-top">
              <a-upload
                name="importfile"
                :action="fileImportUrl"
                :multiple="true"
                :fileList="importFileList"
                :showUploadList="false"
                @change="handleChange4"
              >
                <span v-if="loading">
                  <a-icon type="loading" />正在上传，请稍等
                </span>
                <a-button v-else>
                  <a-icon type="upload" />上传文档
                </a-button>
              </a-upload>
            </div>
            <div class="import-introduce-step-right-bottom">若导入数据超过5000条，请分次导入</div>
          </div>
        </div>
      </div>
    </a-modal>
    <moveclass
      ref="moveclass"
      :list="treeData"
      :products="productdata"
      :selectList="selectedRowKeys"
      @getProduct="getProductLists"
      @clear="clearSelectList"
    ></moveclass>
    <importExcel ref="importExcel" @getProduct="getProductLists"></importExcel>
    <matchCode ref="matchCode"></matchCode>
    <preImgs ref="preImgs"></preImgs>
  </div>
</template>
<script>
import {
  getProductNoTreeList,
  getProductTreeList,
  getProductList,
  checkImportDone,
  deletProducts,
  getSearchCondition,
  deletProduct
} from "@/api/ProductApi";

const columns = [
  {
    title: "零部件编码",
    dataIndex: "productCode",
    key: "productCode",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productCode" }
  },
  {
    title: "零部件描述",
    dataIndex: "productDescribe",
    key: "productDescribe",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productDescribe" }
  },
  {
    title: "品名",
    dataIndex: "productName",
    key: "productName",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productName" }
  },
  {
    title: "品牌",
    dataIndex: "productBrand",
    key: "productBrand",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productBrand" }
  },
  {
    title: "规格",
    dataIndex: "productSpecs",
    key: "productSpecs",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productSpecs" }
  },
  {
    title: "材质",
    dataIndex: "productMaterialQuality",
    key: "productMaterialQuality",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productMaterialQuality" }
  },
  {
    title: "印记",
    dataIndex: "productImprint",
    key: "productImprint",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productImprint" }
  },
  {
    title: "图纸",
    dataIndex: "productChart",
    key: "productChart",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productChart" }
  },
  {
    title: "长度",
    dataIndex: "productLength",
    key: "productLength",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "productLength" }
  },
  {
    title: "包装规格",
    dataIndex: "packStandards",
    key: "packStandards",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "packStandards" }
  },
  {
    title: "操作",
    width: 200,
    align: "center",
    scopedSlots: { customRender: "action" }
  }
];

import screen from "@/views/products/filter";
import breadcrumb from "@/views/products/breadcrumb";
import Menu from "@/views/products/menu";
import moveclass from "@/views/products/moveclass";
import importExcel from "@/views/products/import_excel";
import matchCode from "@/views/products/match_code";
import preImgs from "@/views/products/imgs_preview";
export default {
  components: {
    screen,
    breadcrumb,
    Menu,
    moveclass,
    importExcel,
    matchCode,
    preImgs
  },
  data() {
    return {
      screeningList: [],
      breadcrumb: null,
      curtab1: "全部",
      curtab2: "全部",
      curtab3: "全部",
      importFileList: [],
      checkedKeys: [],
      treeData: [],
      treeData1: [],
      confirmLoading: false,
      expandedKeys: ["0"],
      autoExpandParent: true,
      defaultExpandAll: false,
      selectedKeys: 0,
      showPagination: false,
      pageSize: 20,
      current: 1,
      pageTotal: 0,
      productdata: [],
      columns,
      productDiameter: "",
      productMaterialQualitys: "",
      productStandards: "",
      productDiameterDate: [],
      productMaterialQualitysDate: [],
      productStandardsDate: [],
      selectedSearchConditions: ["w1"],

      selectedRowKeys: [],
      selectedProductRows: [],
      batchDeleteForm: { ids: [] },

      visible5: false,
      visible: false,
      loading: false,

      Title1: "螺栓",
      productImage:
        "http://cbu01.alicdn.com/img/ibank/2018/784/537/8825735487_772937239.230x230.jpg",
      searchKey: "",
      productParams: [
        {
          key: "标准",
          choose: [],
          items: []
        },
        {
          key: "直径",
          choose: [],
          items: []
        },
        {
          key: "材质",
          choose: [],
          items: []
        }
      ]
    };
  },
  watch: {},
  computed: {
    fileImportUrl() {
      return (
        this.$global.baseUrl +
        "/parts/import" +
        "?access_token=" +
        this.$global.access_token
      );
    },
    fileImportUrl2() {
      return (
        this.$global.baseUrl +
        "/parts/create-excel" +
        "?access_token=" +
        this.$global.access_token
      );
    }
  },
  mounted() {
    this.breadcrumb = this.$refs.breadcrumb;
    this.getProductTreeLists();
  },
  methods: {
    //获取分类树无主节点列表
    getProductNoTreeLists() {
      getProductNoTreeList().then(
        res => {
          let arr = [];
          if (res.data.code === this.$global.successCode) {
            //console.log("数据请求成功", res);
            res.data.data.forEach(element => {
              arr.push(element);
            });
            this.treeData1 = arr;
          } else {
            this.$message.error(res.data.message);
          }
        },
        function(error) {
          console.log("请求数据失败", error);
        }
      );
    },
    //获取分类树有主节点
    getProductTreeLists() {
      getProductTreeList().then(
        res => {
          this.DefaultExpandAll = true;
          if (res.data.code === this.$global.successCode) {
            //console.log("数据请求成功", res);
            //console.log("数据请求成功", res);
            this.treeData.push(res.data.data);
            this.$refs.menu.init();
          } else {
            this.$message.error(res.data.message);
          }
        },
        function(error) {
          console.log("请求数据失败", error);
        }
      );
    },

    //点击树节点操作
    onExpand(expandedKeys) {
      //console.log('onExpand', expandedKeys)
      // if not set autoExpandParent to false, if children expanded, parent can not collapse.
      // or, you can remove all expanded children keys.
      this.expandedKeys = expandedKeys;
      this.autoExpandParent = false;
    },
    onSelectree(selectedKeys, info) {
      this.selectedKeys = selectedKeys;
      this.getProductLists();
      this.$refs.filter.getConditions(selectedKeys);
    },
    setScreenList(list) {
      this.screeningList = list;
      this.getProductLists();
    },
    //获取主零部件列表
    getProductLists() {
      this.productdata = [];
      getProductList({
        claCode: this.selectedKeys,
        search: this.searchKey,
        page: this.current,
        pageSize: this.pageSize,
        screeningList: this.screeningList
      }).then(
        res => {
          if (res.data.code === this.$global.successCode) {
            //console.log("数据请求成功", res);
            this.productdata = res.data.data.list;
            this.pageTotal = res.data.data.total;
          } else {
            this.$message.error(res.data.message);
          }
        },
        function(error) {
          console.log("请求数据失败", error);
        }
      );
    },

    //获取查询条件
    getSearchConditions(corCode) {
      let params = this.productParams;
      this.productDiameterDate = [];
      this.productMaterialQualitysDate = [];
      this.productStandardsDate = [];
      // this.productdata = [];
      getSearchCondition(corCode).then(
        res => {
          if (res.data.code === this.$global.successCode) {
            let data = res.data.data;
            //console.log("数据请求成功", res);
            if (data != null && data[0]) {
              params[0].items = params[0].choose =
                data[0].product_standard || [];
              params[1].items = params[1].choose =
                data[1].product_diameter || [];
              params[2].items = params[2].choose =
                data[2].product_material_quality || [];
              this.productDiameterDate = res.data.data[0].product_diameter;
              this.productMaterialQualitysDate =
                res.data.data[2].product_material_quality;
              this.productStandardsDate = res.data.data[1].product_standard;
            } else {
              params[0].items = params[0].choose = [];
              params[1].items = params[1].choose = [];
              params[2].items = params[2].choose = [];
            }
            this.productParams = params;
          } else {
            this.$message.error(res.data.message);
          }
        },
        function(error) {
          console.log("请求数据失败", error);
        }
      );
    },

    onChange(current) {
      this.current = current;
      //console.log(this.current)
      this.getProductLists();
    },
    onShowSizeChange(current, pageSize) {
      this.pageSize = pageSize;
      //console.log(this.pageSize)
      this.getProductLists(this.selectedKeys);
    },

    changeclaId() {
      // console.log(arguments[0])
      this.curclaCode = arguments[0];
      this.ProductForm.claCode = Number(arguments[0]);

      //console.log("===========分类编码claCode:");
      //console.log(this.ProductForm);
    },

    //批量导入数据
    handleChange4(info) {
      //console.log(info);
      if (info.file.status == "done") {
        if ((info.file.response.code = this.$global.successCode)) {
          this.$message.success(info.file.response.message);
        } else {
          this.$message.error(info.file.response.message);
        }
        this.loading = false;
        this.getProductLists(0);
        this.visible5 = false;
      }
      let fileList = [...info.fileList];
      fileList = fileList.slice(-2);
      fileList = fileList.map(file => {
        return file;
      });
      this.importFileList = fileList;
    },

    //批量删除
    onSelectChange(selectedRowKeys) {
      //console.log('selectedRowKeys changed: ', selectedRowKeys);
      this.selectedRowKeys = selectedRowKeys;
    },
    clearSelectList() {
      this.selectedRowKeys = [];
    },
    batchDelete() {
      // ajax request after empty completing
      setTimeout(() => {
        this.selectedRowKeys = [];
      }, 1000);

      this.batchDeleteForm.ids = [];
      this.batchDeleteForm.ids = this.productdata
        .filter((v, i) => this.selectedRowKeys.some(m => m == i))
        .map(v => v.productId);
      if (this.batchDeleteForm.ids.length != 0) {
        deletProducts(this.batchDeleteForm).then(
          res => {
            if (res.data.code === this.$global.successCode) {
              //console.log("删除联系方式数据成功",res);
              this.$message.success("批量删除成功!");
              this.getProductLists(0);
            } else {
              this.$message.error(res.data.message);
            }
          },
          function(error) {
            console.log("批量删除失败", error);
          }
        );
      } else {
        this.$message.warning("请选择要删除的数据!");
      }
    },

    deleteProductOnly(corRow) {
      deletProduct(corRow.productId).then(
        res => {
          if (res.data.code === this.$global.successCode) {
            //console.log("删除联系方式数据成功",res);
            this.$message.success("数据删除成功!");
            this.getProductLists(0);
          } else {
            this.$message.error(res.data.message);
          }
        },
        function(error) {
          console.log("数据删除失败", error);
        }
      );
    },

    opp(record) {
      let pane = {};
      (pane.title = "零部件" + record.productId + "-详情"),
        (pane.content = record.productName + "-详情"),
        (pane.type = 1);
      pane.key = record.productId;

      this.$emit("taboperation", pane);
    },

    opp1() {
      let pane = {};
      (pane.title = "添加零部件"),
        (pane.content = "添加零部件"),
        (pane.type = 3);
      pane.key = 3;

      this.$emit("taboperation", pane);
    },

    opp2() {
      let pane = {};
      (pane.title = "高级匹配查询"),
        (pane.content = "高级匹配查询"),
        (pane.type = 4);
      pane.key = 4;

      this.$emit("taboperation", pane);
    },

    onSearch(value) {}
  }
};
</script>

<style scoped>
.title {
  font-size: 18px;
  padding: 0px 20px 15px;
}
.ant-breadcrumb {
  padding: 0;
  position: absolute;
  top: 10px;
  right: 20px;
}
</style>