<template>
  <div class="pt-10">
    <submenu
      ref="submenu"
      v-model="selectKey"
      :menus="menus"
      :tip="tip"
      @select="select"
      :selectKey="selectKey"
    />
  </div>
</template>
<script>
import submenu from "./submenu";
export default {
  components: {
    submenu
  },
  props: ["bread", "tip", "list"],
  data() {
    return {
      selectKey: "0",
      selectItem:null,
      breadcrumb: [],
      menus: []
    };
  },
  // watch: {
  //   // bread(){
  //   //   console.warn('menu watch')
  //   //   console.log(this.bread)
  //   // }
  //   list() {
  //     // console.warn("watch list");
  //     this.init();
  //   }
  // },
  // mounted() {
  //   // console.warn('menu mounted')
  //   // this.init();
  //   // console.log(this.tip)
  // },
  methods: {
    init() {
      this.selectKey = "0";
      this.menus = this.list;
      this.select(this.selectKey, this.menus[0],true);
    },
    select(key = "0", item = null, init = false) {
      if (!init&&this.selectKey == key) return;
      this.selectKey = key;
      this.selectItem=item;
      if (this.bread) {
        this.bread.getList(this.menus, key);
        this.$emit("select", item.key);
      }
    }
  }
};
</script>
<style lang="less" scoped>
.pt-10 {
  padding-top: 10px;
}
</style>

