<template>
  <a-modal :title="title" :visible="visible" :footer="null" @cancel="visible=false">
    <a-carousel arrows dotsClass="slick-dots slick-thumb">
      <a slot="customPaging" slot-scope="v">
        <img :src="data[v.i].pictureUrl" :alt="data[v.i].fileName" />
      </a>
      <div v-for="(v,i) in data" :key="i">
        <img :src="v.pictureUrl" :alt="v.fileName" />
      </div>
    </a-carousel>
  </a-modal>
</template>
<script>
const baseUrl =
  "https://raw.githubusercontent.com/vueComponent/ant-design-vue/master/components/vc-slick/assets/img/react-slick/";
import { getPreImgs } from "@/api/product";
export default {
  data() {
    return {
      visible: false,
      data: [],
      title: "",
      baseUrl
    };
  },
  methods: {
    init(v) {
      this.data = [];
      this.visible = true;
      this.title = v.productName;
      this.getData(v.productCode);
    },
    async getData(id) {
      let res = await getPreImgs(id);
      if (res.data.code != this.$global.successCode)
        return this.$message.warning(res.data.message);
      this.data = res.data.data || [];
    }
  }
};
</script>
<style scoped>
/* For demo */
.ant-carousel >>> .slick-dots {
  margin-top: 20px;
  position: relative;
  height: auto;
}
.ant-carousel >>> .slick-slide img {
  border: 5px solid #fff;
  display: block;
  margin: auto;
  max-width: 80%;
}
.ant-carousel >>> .slick-thumb li {
  width: 60px;
  height: 45px;
}
.ant-carousel >>> .slick-thumb li img {
  width: 100%;
  height: 100%;
  filter: grayscale(100%);
}
.ant-carousel >>> .slick-thumb li.slick-active img {
  filter: grayscale(0%);
}
</style>