<template>
  <div>
    <div
      class="conditions"
      :style="v.open?'max-height:50px':'max-height:200px'"
      v-for="(v,i) in conditions"
      :key="i"
    >
      <div class="left">
        <div class="li">{{v.title}}：</div>
        <ul class="params">
          <li class="li pointer" :class="{'select':v.isAll}" @click="chooseAll(i)">全部</li>
          <li
            class="li pointer"
            :class="{'select':m.state}"
            @click="chooseOne(i,n)"
            v-for="(m,n) in v.params"
            :key="n"
          >{{m.name}}</li>
        </ul>
      </div>
      <div class="fold pointer" @click="v.open=!v.open">
        {{v.open?'展开':'收起'}}
        <a-icon class="icon" :class="{'rotate':!v.open}" type="right" />
      </div>
    </div>
    <div class="choose-condition">
      <div>已选条件</div>
      <div class="items">
        <template v-for="(m,n) in conditions">
          <div
            @click="chooseOne(n,i)"
            class="item pointer"
            v-for="(v,i) in m.params"
            :key="n+'-'+i"
            v-show="v.state"
          >
            {{v.name}}
            <b class="icon">X</b>
          </div>
        </template>
        <div @click="formatList();setChooses()" class="item pointer clear">清除全部</div>
      </div>
    </div>
  </div>
</template>
<script>
import { getFilters } from "@/api/product";
import { parse } from "path";
export default {
  data() {
    return {
      conditions: []
    };
  },
  methods: {
    async getConditions(id = 0) {
      let res = await getFilters(id);
      if (res.data.code != this.$global.successCode) return;
      this.formatList(res.data.data);
      return;
      let data = [
        {
          title: "标准",
          open: false,
          params: [
            {
              name: "DIN125A型",
              state: false
            }
          ]
        }
      ];
      this.conditions = data;
    },
    formatList(list = this.conditions) {
      list.forEach(v => {
        v.title = v.name;
        v.open = false;
        v.isAll = true;
        v.params = v.conditionList||[];
        v.params.forEach(m => {
          m.name = m.condValue;
          m.state = false;
        });
      });
      this.conditions = list;
    },
    setChooses() {
      let data = JSON.parse(JSON.stringify(this.conditions)),
        list = [];
      list = data.filter(v => !v.isAll);
      list.forEach(v => {
        v.conditionList = v.params.filter(m => m.state);
      });
      this.$emit("select", list);
    },
    chooseOne(i, n) {
      let data = this.conditions,
        list = data[i],
        param = list.params[n];
      param.state = !param.state;
      list.isAll = list.params.every(m => !m.state);
      // list.isAll = list.params.every(m => m.state)||list.params.every(m => !m.state);
      // if (list.isAll) {
      //   list.params.forEach(v => {
      //     v.state = false;
      //   });
      // }
      this.setChooses();
    },
    setAll(i) {
      let data = this.conditions,
        list = data[i];
      list.isAll = list.params.every(m => m.state);
      if (list.isAll) {
        list.params.forEach(v => {
          v.state = false;
        });
      }
    },
    chooseAll(i) {
      let data = this.conditions,
        target = data[i];
      target.params.forEach(v => {
        v.state = false;
      });
      target.isAll = true;
      this.setChooses();
    }
  }
};
</script>
<style lang="less" scoped>
.conditions {
  transition: all 0.5s;
  overflow: hidden;
  padding: 0.625rem;
  display: flex;
  align-items: flex-start;
  border-bottom: 1px solid #ddd;
  .left {
    > :first-child {
      flex-shrink: 0;
      padding-left: 0;
    }
    display: flex;
    flex-grow: 1;
    align-items: flex-start;
  }
  .li {
    padding: 0.25rem 0.625rem;
    border-radius: 0.25rem;
    margin: 0.3125rem;
    margin-left: 0;
  }
  .params {
    display: flex;
    flex-wrap: wrap;
    .select,
    li:hover {
      background-color: #00aaee;
      color: #fff;
    }
  }
  .fold {
    margin-top: 0.5rem;
    display: flex;
    flex-shrink: 0;
    padding: 0 0.3125rem;
    align-items: center;
    border: 1px solid #ddd;
    .icon {
      margin-left: 0.3125rem;
      transition: all 0.5s;
      font-size: 0.625rem;
    }
    .rotate {
      transform: rotate(90deg);
    }
  }
}
.choose-condition {
  padding: 0.625rem;
  display: flex;
  align-items: flex-start;
  > :first-child {
    flex-shrink: 0;
    padding: 0.3125rem 0.625rem;
    margin: 0.3125rem;
    margin-left: 0;
    padding-left: 0;
  }
  .items {
    display: flex;
    flex-grow: 0;
    flex-wrap: wrap;
    .item {
      background-color: #eee;
      padding: 0.3125rem 0.625rem;
      margin: 0.3125rem;
    }
    .item:hover {
      background-color: #ddd;
    }
    .icon {
      font-size: 0.625rem;
      font-weight: bold;
      color: #ff0036;
      margin-left: 0.3125rem;
    }
    .clear {
      color: #ff0036;
      background-color: transparent;
    }
  }
}
.pointer {
  cursor: pointer;
}
</style>

