<template>
  <a-modal
    width="580px"
    :title="'批量导入'+(isCode?'编码对应':'')"
    :visible="visible"
    :footer="null"
    @ok="handleOk"
    @cancel="handleCancel"
  >
    <a-steps :current="current" size="small">
      <a-step v-for="item in steps" :key="item.title" :title="item.title" />
    </a-steps>
    <div v-show="current==0" class="upload">
      <div class="item">
        <a-icon class="icon" type="cloud-download" />
        <ul class="text">
          <li class="t-title">填写导入{{isCode?'内部编码对应表':'零部件分类的信息'}}</li>
          <li class="explain">请按照数据模板的格式准备导入数据,模板中的名称不可更改,表头行不能删除</li>
          <li>
            <span class="download" @click="download()">下载模板</span>
          </li>
        </ul>
      </div>
      <div class="item">
        <a-icon class="icon" type="cloud-upload" />
        <ul class="text">
          <li class="t-title">上传填好的{{isCode?'内部编码对应表':'零部件分类表'}}</li>
          <li class="explain">文件后缀名必须为xls或xlsx(即excel格式),大小不得大于10M</li>
          <li>
            <a-upload
              :action="$global.baseUrl +(isCode?'/relation':'/parts')+'/import?access_token='+$global.access_token"
              :multiple="true"
              @change="upload"
              :showUploadList="false"
              :beforeUpload="beforeUpload"
            >
              <span class="download">上传文件</span>
            </a-upload>
          </li>
        </ul>
      </div>
    </div>
    <div v-show="current==1" class="upload">
      <div class="item">
        <a-icon class="icon" type="check-circle" />
        <ul class="text">
          <li class="t-title">成功</li>
          <li>
            <span class="download">{{uploadData.add}}条</span>
          </li>
        </ul>
      </div>
      <div class="item">
        <a-icon class="icon" type="exclamation-circle" />
        <ul class="text">
          <li class="t-title">失败</li>
          <li>
            <span class="download" @click="download(uploadData.url)">{{uploadData.fal}}条  点击下载</span>
          </li>
        </ul>
      </div>
      <div class="step2">
        <a-button @click="current++" type="primary">下一步</a-button>
        <a-button @click="current--">返回重新上传</a-button>
      </div>
    </div>
    <div v-show="current==2" class="step3">
      <a-icon class="succ" type="check-circle" theme="filled" />
      <p>批量导入完成</p>
      <p>成功导入{{uploadData.add}}条</p>
      <a-button type="primary" @click="visible=false">完成</a-button>
    </div>
  </a-modal>
</template>
<script>
export default {
  props: [],
  data() {
    return {
      visible: false,
      isCode: false,
      current: 1,
      uploadSize: 1024 * 1024 * 10,
      uploadData: {},
      steps: [
        {
          title: "上传文件"
        },
        {
          title: "执行导入"
        },
        {
          title: "导入完成"
        }
      ]
    };
  },
  methods: {
    init(isCode = false) {
      this.current = 0;
      this.isCode = isCode;
      this.visible = true;
      this.uploadData = {};
    },
    handleOk() {},
    handleCancel() {
      this.visible = false;
    },
    beforeUpload(file, files) {
      if (
        file.type != "application/vnd.ms-excel" &&
        file.type !=
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
      ) {
        this.$message.error("文件后缀名必须为xls或xlsx(即excel格式)");
        return false;
      }
      if (file.size > this.uploadSize) {
        this.$message.error("大小不得大于10M");
        return false;
      }
      return true;
    },
    upload(info) {
      // if (info.file.status !== "uploading") {
      //   console.log(info.file, info.fileList);
      // }
      if (info.file.status === "done") {
        if (info.file.response.code == this.$global.successCode) {
          let data = info.file.response.data;
          this.$message.success(data.message);
          this.uploadData = data;
          // this.$message.success(`${info.file.name} file uploaded successfully`);
          this.current = 1;
          if(data.add>0)
          this.$emit("getProduct");
        } else {
          this.$message.error(info.file.response.message);
        }
      } else if (info.file.status === "error") {
        this.$message.error(`${info.file.name} file upload failed.`);
      }
    },
    download(url = null) {
      let href =
        this.$global.baseUrl +
        (this.isCode ? "/relation" : "/parts") +
        "/create-excel?access_token=" +
        this.$global.access_token;
      if (url) href = url+'?access_token='+this.$global.access_token;
      const elink = document.createElement("a");
      elink.style.display = "none";
      elink.href = href;
      document.body.appendChild(elink);
      elink.click();
      document.body.removeChild(elink);
    }
  }
};
</script>
<style lang="less" scoped>
@size: 5.625rem;
@border-color: #c9c9c9;
@icon-color: #f2f2f2;
@primary-color: #00aaee;
.upload {
  .item {
    margin-top: 30px;
    border: 1px solid @border-color;
    height: @size;
    display: flex;
    .icon {
      display: flex;
      flex-shrink: 0;
      justify-content: center;
      align-items: center;
      font-size: 45px;
      background-color: @icon-color;
      color: @border-color;
      border-right: 1px solid @border-color;
      width: @size;
    }
    .text {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      padding: 0.625rem;
      font-size: 8px;
      .t-title {
        font-size: 10px;
        font-weight: bolder;
      }
      .explain {
        color: @border-color;
      }
      .download {
        color: @primary-color;
        cursor: pointer;
      }
      .download:hover {
        opacity: 0.8;
      }
    }
  }
  .step2 {
    margin-top: 30px;
    text-align: center;
    button + button {
      margin-left: 10px;
    }
  }
}
.step3 {
  min-height: 200px;
  text-align: center;
  .succ {
    margin: 20px 0;
    color: #4bd863;
    font-size: 60px;
  }
  p {
    margin-bottom: 10px;
  }
}
</style>
