<template>
  <div class="box" v-show="list[0]">
      <span class="item" v-for="(v,i) in list">{{v}}</span>
  </div>
</template>
<script>
export default {
  data(){
      return {
          list:[]
      }
  },
  // mounted(){
  //     console.warn('breadcrumb mounted')
  // },
  methods: {
    getList(target, keys,len=3) {
      let arr = keys.split("-"),
        bread = [];
      arr.forEach(v => {
        bread.push(target[v].title);
        target = target[v].children;
      });
      this.list = bread.length>len?bread.splice(len-1):[];
    }
  }
};
</script>
<style lang="less" scoped>
.box{
    border-bottom: 1px solid #ddd;
    padding: .625rem;
    .item:not(:last-child)::after{
        content:'>';
        margin:0 1rem;
        font-weight: bold;
        color:#ddd;
    }
}
</style>
