import http from '@/utils/AxiosRequest';

export let getFilters = (claCode) => {
    return http({ url: `/screening/list/${claCode}`, method: 'GET' })
}
export let batchMove = (data) => {
    return http({ url: `/parts/move`, data, method: 'post' })
}
export let getMatch = (productCode) => {
    return http({ url: `/relation`, params: { productCode }, method: 'get' })
}
export let putMatch = (data) => {
    return http({ url: `/relation`, data, method: 'put' })
}
export let getCompany = (search='') => {
    return http({ url: `/company`, params: { search }, method: 'get' })
}
export let getPreImgs = (productCode) => {
    return http({ url: `/drawing`, params: { productCode }, method: 'get' })
}
